import React, { Component } from 'react';
import './App.css';

import { BrowserRouter } from 'react-router-dom';

import Menu from './components/Menu/index';
import { Content } from './components/Content';

class App extends Component {
  constructor() {
    super();

    this.state = {
      visible: false,
      opacity: 1,
      height: null,
      scale: null,
      offset: false
    };
  }

  componentDidMount() {
    this.setHeight();

    document.addEventListener('touchstart', e => e.preventDefault());

    window.addEventListener('resize', this.setHeight);
    window.addEventListener('mousemove', this.onMouseMove);
    window.addEventListener('devicemotion', e => this.onDeviceMove(e));
  }

  setOffset = offset => {
    this.setState({ offset: offset });
  }

  onTouch = e => e.preventDefault();

  setHeight = () => {
    this.setState({ height: window.innerHeight });
  }

  onDeviceMove(e) {
    this.setState({
      x: e.accelerationIncludingGravity.x * 2,
      y: e.accelerationIncludingGravity.y * 2,
      scale: 1.09
    });
  }

  onMouseMove = e => {
    this.setState({
      x: (e.clientX - window.innerWidth / 2) / 40,
      y: (e.clientY - window.innerHeight / 2) / 40,
      scale: 1.03
    });
  }

  render() {
    return (
      <BrowserRouter basename={'/portfolio-alpha'}>
        <div className="App">
          <div className="back" style={{height: this.state.height, transform: `scale(${this.state.scale}) translate(${this.state.x}px, ${this.state.y}px)`}}></div>

          <div>
            <Menu offset={this.setOffset} height={this.state.height - 76}/>
          </div>

          <div className={ this.state.offset ? 'container zoom' : 'container' } style={{height: this.state.height - 76}}>
            <Content/>
          </div>
        </div>
      </BrowserRouter>
    );
  }
}

export default App;



