import React, { Component } from 'react';
import Delay from 'react-delay';

class Contact extends Component {
  render() {
    return (
      <div>
        <Delay wait={100}><h2>Kontakt</h2></Delay>

        <div className="inner">
          <form>
            <div>
              <Delay wait={200}><input type="text" placeholder="Imię i nazwisko" required/></Delay>
              <Delay wait={400}><input type="email" placeholder="e-mail" required/></Delay>
              <Delay wait={600}><input type="text" placeholder="Temat" required/></Delay>
            </div>
            <Delay wait={800}><textarea placeholder="Treść wiadomości" required></textarea></Delay>
            <Delay wait={1000}><button type="submit">Wyślij</button></Delay>
          </form>
        </div>
      </div>
    );
  }
}

export default Contact;



